import { BaseModel, Result } from '@models';
import { DataService } from '@services';
import { ApplicationState } from '@store';
import { batch } from 'react-redux';
import { BaseActions } from '../Base';
import { Actions } from './state';

export class DataActions<T extends BaseModel> {
  getById = (entity: string, id: string) => async (dispatch, getState: () => ApplicationState) => {
    let result: T;
    await batch(async () => {
      await dispatch({ type: Actions.RequestItemData });
      var result = await DataService.getById<T>(entity, id);
      await dispatch({
        type: Actions.ReceiveItemData,
        entityName: entity,
        payload: result,
      });
    });
    return result;
  };

  getList =
    (entity: string, cache: boolean = false) =>
    async (dispatch, getState: () => ApplicationState) => {
      let result: T[];
      await batch(async () => {
        await dispatch({ type: Actions.RequestListData });
        const getData = async () => {
          result = await DataService.getList<T>(entity);
          await dispatch({
            type: Actions.ReceiveListData,
            entityName: entity,
            payload: result,
          });
        };
        if (cache) {
          const state = getState();
          if (state.Data[entity].List.length > 0) {
            await dispatch({
              type: Actions.ReceiveListData,
              entityName: entity,
              payload: state.Data[entity].List,
            });
            result = [];
          } else {
            await getData();
          }
        } else {
          await getData();
        }
      });
      return result;
    };

  createItem =
    (entity: string, item: any) => async (dispatch, getState: () => ApplicationState) => {
      let result: T;
      await batch(async () => {
        await dispatch({ type: Actions.RequestCreateData });
        delete item.Id;
        result = await DataService.create<T>(entity, item);
        await dispatch({
          type: Actions.ReceiveCreateData,
          entityName: entity,
          payload: result,
        });
      });
      return result;
    };

  updateItem =
    (entity: string, item: any) => async (dispatch, getState: () => ApplicationState) => {
      let result: T;
      await batch(async () => {
        await dispatch({ type: Actions.RequestUpdateData });

        result = await DataService.update<T>(entity, item);
        await dispatch({
          type: Actions.ReceiveUpdateData,
          entityName: entity,
          payload: result,
        });
      });
      return result;
    };

  deleteItem =
    (entity: string, id: string) => async (dispatch, getState: () => ApplicationState) => {
      let result: T;
      await batch(async () => {
        await dispatch({ type: Actions.RequestDeleteData });

        result = await DataService.delete<T>(entity, id);
        await dispatch({
          type: Actions.ReceiveDeleteData,
          entityName: entity,
          id,
        });
      });
      return result;
    };

  clearCache = (entity: string) => async (dispatch, getState: () => ApplicationState) => {
    await dispatch({ type: Actions.ClearCache, entityName: entity });
  };
}
