import { Product } from '@models';
import { DataActions } from '@reducers';
import { DataService } from '@services';
import { ApplicationState } from '@store';
import * as React from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { withRouter } from '../../withRouter';

interface ProductsProps {
  DataActions: DataActions<Product>;
}

type Props = ProductsProps & ApplicationState;

class Products extends React.Component<Props, any> {
  constructor(props) {
    super(props);
  }

  async componentDidMount() {
    await this.props.DataActions.getList('Product');
  }

  render() {
    return (
      <React.Fragment>
        <section className="rooms mt50">
          <div className="container">
            <div className="row">
              <div className="col-sm-12">
                <h2 className="lined-heading">
                  <span>Sayfa 2</span>
                </h2>
              </div>
            </div>
          </div>
        </section>
      </React.Fragment>
    );
  }
}

var component = withRouter(
  connect(
    (state: ApplicationState) => state,
    (dispatch) => {
      return {
        DataActions: bindActionCreators({ ...new DataActions<Product>() }, dispatch),
      };
    }
  )(Products) as any
);

export default component as any;
