import { Product } from '@models';
import { DataActions } from '@reducers';
import { ApplicationState } from '@store';
import * as React from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { withRouter } from '../../withRouter';
import * as faceDetection from '@tensorflow-models/blazeface';
import * as tf from '@tensorflow/tfjs-core';
// Register one of the TF.js backends.
import '@tensorflow/tfjs-backend-webgl';
import * as mpPose from '@mediapipe/pose';

interface HomeProps {
  DataActions: DataActions<Product>;
}

type Props = HomeProps & ApplicationState;

interface HomeState {
  file?: File;
  faceDetector?: faceDetection.BlazeFaceModel;
  context?: CanvasRenderingContext2D;
}
class Home extends React.Component<Props, HomeState> {
  constructor(props) {
    super(props);
    this.handleFileChange = this.handleFileChange.bind(this);
    this.handleVideoLoad = this.handleVideoLoad.bind(this);
    this.runFrame = this.runFrame.bind(this);
    this.cancelFrame = this.cancelFrame.bind(this);
    this.handleWebcamButton = this.handleWebcamButton.bind(this);

    this.videoElement = React.createRef<HTMLVideoElement>();
    this.canvasElement = React.createRef<HTMLCanvasElement>();
    this.state = {
      file: null,
      faceDetector: null,
    };
  }

  videoElement: React.RefObject<HTMLVideoElement>;
  canvasElement: React.RefObject<HTMLCanvasElement>;
  ctx: CanvasRenderingContext2D;
  animationFrame;

  async componentDidMount() {
    const faceDetector = await faceDetection.load();
    this.setState({ faceDetector });
    await this.handleWebcamButton();
  }

  handleFileChange(ev: React.ChangeEvent<HTMLInputElement>) {
    this.cancelFrame();
    this.videoElement.current.srcObject = null;
    this.setState({ file: ev.target.files[0] });
    this.videoElement.current.load();
  }

  async handleVideoLoad() {
    this.ctx = this.canvasElement.current.getContext('2d');
    this.cancelFrame();
    await this.videoElement.current.play();
    this.canvasElement.current.width = this.videoElement.current.videoWidth;
    this.canvasElement.current.height = this.videoElement.current.videoHeight;
  }

  async runFrame() {
    const returnTensors = false;
    const flipHorizontal = false;
    const annotateBoxes = false;
    const pose: any = await this.state.faceDetector.estimateFaces(
      this.videoElement.current,
      returnTensors,
      flipHorizontal,
      annotateBoxes
    );

    if (pose.length > 0) {
      this.ctx.clearRect(0, 0, this.canvasElement.current.width, this.canvasElement.current.height);

      for (let i = 0; i < pose.length; i++) {
        const start = pose[i].topLeft;
        const end = pose[i].bottomRight;
        const size = [end[0] - start[0], end[1] - start[1]];
        this.ctx.fillStyle = 'red';
        this.ctx.drawImage(
          this.videoElement.current,
          start[0],
          start[1],
          size[0],
          size[1],
          0,
          0,
          100,
          100
        );
        // this.ctx.fillRect(start[0], start[1], size[0], size[1]);
      }
    }

    this.animationFrame = requestAnimationFrame(this.runFrame.bind(this));
  }

  cancelFrame() {
    if (this.animationFrame) {
      cancelAnimationFrame(this.animationFrame);
      this.animationFrame = null;
    }
  }

  async handleWebcamButton() {
    const mediaDevices = navigator.mediaDevices;
    const stream = await mediaDevices.getUserMedia({
      video: true,
      audio: false,
    });
    this.cancelFrame();
    this.setState({ file: null });
    this.videoElement.current.srcObject = stream;
  }

  render() {
    return (
      <section className="rooms mt50">
        <div className="container">
         
          {/* <div className="row">
            <div className="col-sm-6">
              <div className="row">
                <div className="col-sm-6">
                  <input
                    type="file"
                    name="video"
                    accept="video/*"
                    onChange={this.handleFileChange}
                  />
                </div>
                <div className="col-sm-6">
                  <button onClick={this.handleWebcamButton}>Open WebCam</button>
                </div>
              </div>
            </div>
          </div> */}
          <div className="row">
            <div className="col-sm-6">
              <video
                onPause={this.cancelFrame}
                onPlay={this.runFrame}
                controls
                playsInline
                autoPlay={false}
                style={{ width: 'auto', height: 'auto' }}
                ref={this.videoElement}
                src={this.state.file ? URL.createObjectURL(this.state.file) : null}
                onLoadedData={this.handleVideoLoad}
              ></video>
            </div>
            <div className="col-sm-6">
              <canvas ref={this.canvasElement}></canvas>
            </div>
          </div>
        </div>
      </section>
    );
  }
}

var component = withRouter(
  connect(
    (state: ApplicationState) => state,
    (dispatch) => {
      return {
        DataActions: bindActionCreators({ ...new DataActions<Product>() }, dispatch),
      };
    }
  )(Home) as any
);

export default component as any;
